header{
    height: 100vh;
    padding-top: 5rem;
    margin-bottom: 10rem;
   
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/* ============ CTA ===========*/
.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/* ============ HEADER SOCIALS ===========*/
 .header__socials {
    
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    position: absolute;
    left: 0%;
    bottom: 0;
 }

 .header__socials::after {
    content: "";
    width: 1px;
    height: 2rem;
   border-top: 1px solid var(--color-primary);
   color: blue;
 }

 /* ============ ME ===========*/
 .me img {
    width: 24rem;
    height: 24rem;
    position: absolute;
    left: calc(50% - 11rem);
    /*left: 50%;
    transform: translateX(-50%);*/
    margin-top: 4rem;
    /*border-radius: 12rem 12rem 0 0;*/
    border-radius: 50%;
    overflow: hidden;
    
   
 }
 

 /* ============ Scroll Down ===========*/
 .scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
    overflow-x: hidden;
  

 }

 /* ================= MEDIA QUERIES (MEDIUM DEVICES) ================*/
@media screen and (max-width: 1024px) {
    header {
        height: 68vh;
    }

}

 /* ================= MEDIA QUERIES (SMALL DEVICES) ================*/
 @media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }

    .header__socials,
    .scroll__down{
        display: none;
    }
    
}